        display: flex;
        align-items: center;

        span{
            font-weight: 600;
            font-size: 1.2rem;
            letter-spacing: 3px;
            height: 40px;
            width: 40px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
    .vspButton {
        height: 40px;
        width: 40px;
        border-radius: 20px;
        transition: all 0.5s;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 600;
        background-color: #375096;
        box-shadow: 3px 3px 5px -2px rgba(0, 0, 0, 0.44), -3px -3px 5px -2px rgba(255, 255, 255, 0.44), inset -3px -3px 3px -2px rgba(0, 0, 0, 0.44);
        cursor: pointer;
        color: #fff;
        outline: none !important;
        margin-right: 1px;
        margin-left: 1px;

        &:nth-last-child(1){
            margin-left: 15px;
        }
        &:nth-child(1){
             margin-right: 15px;
        }
        &:hover{
            background-color: #d8e8f5;
            color: #000;
        }

        svg{
            stroke-width: 4px;
        }
    }
    .vspButton-selected{
        background-color: #d8e8f5;
        color: #000;
    }
    .vspButtonFast{
        background-color: #d8e8f5;
        color: #b5b9bc;
    }
</style>
